<template>
	<div>
		<h3>未完成</h3>
		<ul>
			<li v-for="item in incomplete" :key="item.id">
				<input type="checkbox" v-model="$store.getters.isShow[item.id]" @change="modifySelect(item.id, item.isShow[item.id])" />
				<span :class="{ boxStyle: item.isShow }">{{ item.text }}</span>
			</li>
		</ul>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
	name: 'incomplete',
	computed: {
		...mapGetters(['incomplete'])
	},
	methods: {
		modifySelect(id, val) {
			this.$store.commit('modifySelected', { id, isShow: val });
		}
	}
};
</script>

<style scoped>
.boxStyle {
	color: #999;
	text-decoration: line-through;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}
li {
	font-size: 20px;
}
</style>
